import React from "react";
import Helmet from "react-helmet";
import Link from 'gatsby-link'

export default function Template({
  data, // this prop will be injected by the GraphQL query we'll write in a bit
}) {
  const { markdownRemark: post } = data; // data.markdownRemark holds our post data
  return (
    <div className="blog-post-container container card">
      <Helmet title={ post.frontmatter.title + ' - ' + data.site.siteMetadata.title } />
      <div className="blog-post">
        <div className="blog-title"><h1 className="title">{post.frontmatter.title}</h1></div>
        
        <div className="post-data">On <Link to={'/journal/' + post.frontmatter.category + '/'}>{post.frontmatter.category}</Link>. <span>{post.frontmatter.date}</span></div>
        
        <div className="introduction">{post.frontmatter.foreword}</div>
        <hr className="separator"/>
        <div
          className="blog-post-content"
          dangerouslySetInnerHTML={{ __html: post.html }}
        />
      </div>
  </div>
  
  );
}

export const pageQuery = graphql`
  query BlogPostByPath($id: String!) {
    site {
      siteMetadata {
        title
        profesion
      }
    },
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "DD MMMM, YYYY")
        path
        title
        category
        foreword
      }
    }
  }
`;



